.head {
    margin: 4rem 0;
    
    a {
        text-decoration: none;
        text-transform: lowercase;
    }

    ul {
        list-style: none;
        padding: 0;
        margin: 0;
        display: flex;
    }

    li {
        margin-right: 15px;
    }
}

.head-inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.head-menu {
    display: flex;
}

.head-logo {
    display: inline-flex;
    height: 105px;
    width: 80px;
}